import { all, takeEvery, put, call, fork } from 'redux-saga/effects';
import actions from './actions';
import PatientHelper from '../../helpers/patientHelper';
import notification from '../../components/notification';

export function* addContact() {
  yield takeEvery(actions.NEW_CONTACT, function*(contacts, newContact) {
    let response = yield call(PatientHelper.add, newContact);

    if(response.result ){
      contacts.forEach(contact => {
        if(contact.id === newContact.id){
          contact.id = response.id;
        }
      });
      yield put({
        type: actions.ADD_SUCCESS,
        contacts,
        selectedId: response.id
      });
    } else {
      notification('error', response.error || response);
      yield put({ type: actions.ADD_ERROR });
    }
  });
}
export function* editContact() {
  yield takeEvery(actions.EDIT_CONTACT, function*(contacts, newContact) {
    let response = yield call(PatientHelper.edit, newContact);

    if(response.result){
      yield put({
        type: actions.EDIT_SUCCESS,
        contacts
      });
    } else {
      notification('error', response.error || response);
      yield put({ type: actions.EDIT_ERROR });
    }
  });
}
export function* deleteContact() {
  yield takeEvery(actions.DELETE__CONTACT, function*() {});
}
export default function* rootSaga() {
  yield all([fork(addContact), fork(editContact), fork(deleteContact)]);
}
