import SuperFetch from './superFetch';

class PatientHelper{

    add = async patient => {
        if (!patient.name || patient.name === 'No Name') {
            return { error: 'Patients detail cannot be saved without a valid name' };
        }
        return await SuperFetch.post('patients/add', patient).then(response => {
            return response;
        });
    }

    delete = async id => {
        return await SuperFetch.post('patient/del/'+id).then(response => {
            return response;
        });
    };

    edit = async patient => {
        if (!patient.name || patient.name === 'No Name') {
            return { error: 'Patients detail cannot be saved without a valid name' };
        }
        return await SuperFetch.post('patient/edit/'+patient.id, patient).then(response => {
            return response;
        });
    }
 }

export default new PatientHelper();